.pageWrapper {
  display: flex;
  min-height: 100vh;
  .main {
    flex-grow: 1;
    padding: 19px 23px;
  }
  .titleWrapper {
    margin-bottom: 15px;
  }
  .usersList {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .usersCount {
    margin-top: 12px;
    font-size: 12px;
    text-align: right;
  }
}